import React, { useState, useEffect, useRef } from 'react';
import '../../styles/Career/Career.css';

const Career = React.forwardRef((props, ref) => {

    const introRef = useRef(null);
    const [isVisible, setIsVisible] = useState(false);
    const [careerData, setcareerData] = useState([]);

    useEffect(() => {
      fetch('./cfg/careerData.cfg')
          .then(res => res.json())
          .then(data => setcareerData(data))
          .catch(err => console.error('careerData.cfg 불러오기 실패:', err));
  }, []);
    
    useEffect(() => {
        const observer = new IntersectionObserver(
          (entries) => {
            entries.forEach((entry) => {
              if (entry.isIntersecting) {
                setIsVisible(true);
              } else {
                setIsVisible(false);
              }
            });
          },
          { threshold: 0.5 }
        );
    
        if (introRef.current) {
          observer.observe(introRef.current);
        }
    
        return () => observer.disconnect();
      }, []);

    return (
        <div className="page-content-3" ref={ref}>
            <h2 className='title'>CARREER</h2>
            <div className={`timeline ${isVisible ? "show" : ""}`} ref={introRef}>
                {careerData.map((step, index) => (
                    <div className="timeline-step" key={index}>
                    <div className={`dot ${index === 0 ? "blink" : ""}`}></div>
                    <div className="content">
                        <div className='content-container'>
                            <p>{step.date}</p>
                            <h3>{step.title}</h3>
                        </div>
                        <p>{step.description}</p>
                    </div>
                    </div>
                ))}
            </div>
        </div>
    );
});

export default Career;