import React, { useState, useEffect, useRef } from 'react';
import Header from '../../components/Layout/header';
import Layout from '../../components/Layout/Layout';
import '../../styles/Home/home.css';
import About from '../About/About';
import Spec from '../Project/Project';
import Career from "../Career/Career";
import Ai from "../Ai/AiPage";
import Down from "../Down/Down";

import Scrollbar, { ScrollbarPlugin } from 'smooth-scrollbar';

class DisableScrollPlugin extends ScrollbarPlugin {
  static pluginName = 'disableScroll';

  transformDelta(delta) {
    if (this.options.disabled) {
      return { x: 0, y: 0 };
    }
    return delta;
  }
}

Scrollbar.use(DisableScrollPlugin);
const Home = () => {

    const [showFirst, setShowFirst] = useState(false);
    const [showSecond, setShowSecond] = useState(false);
    const [showThird, setShowThird] = useState(false);

    const texts = ["열정 있는", "책임감이 강한", "웹을 사랑하는"];
    const [currentIndex, setCurrentIndex] = useState(0);

    const scrollRef = useRef(null);
    const scrollbarRef = useRef(null);

    // 초기 애니메이션
    useEffect(() => {
        const isOnFirstPage = window.location.hash === '' || window.location.hash === '#section01';
        const isAtTop = window.scrollY === 0;

        if (isAtTop && isOnFirstPage) {
            document.body.style.overflow = 'hidden';

            const t1 = setTimeout(() => setShowFirst(true), 800);
            const t2 = setTimeout(() => setShowSecond(true), 1600);
            const t3 = setTimeout(() => setShowThird(true), 2000);

            return () => {
                clearTimeout(t1);
                clearTimeout(t2);
                clearTimeout(t3);
                document.body.style.overflow = '';
            };
        } else {
            setShowFirst(true);
            setShowSecond(true);
            setShowThird(true);
            document.body.style.overflow = '';
        }
    }, []);

    // smooth-scrollbar 적용 및 텍스트 인덱스 변경
    useEffect(() => {
        if (!scrollRef.current) return;
    
        const scrollbar = Scrollbar.init(scrollRef.current, { damping: 0.1 });
        scrollbarRef.current = scrollbar;
    
        if (window.location.hash) {
            const target = document.querySelector(window.location.hash);
            if (target) {
                const targetOffset = target.offsetTop;
                scrollbar.setPosition(0, targetOffset);
            }
        }
    
        scrollbar.addListener(({ offset }) => {
            const scrollY = offset.y;
            const sectionHeight = window.innerHeight * 0.08;
            let index = Math.floor(scrollY / sectionHeight);
            index = Math.max(0, Math.min(index, texts.length - 1));
            setCurrentIndex(index);
        });
    
        return () => {
            scrollbar.destroy();
        };
    }, [texts.length]);

    return (
        <Layout>
            <div className="scroll-container" ref={scrollRef} style={{ height: '100vh' }}>
                {/* 첫 번째 페이지 */}
                <div className="page-section page-1" id="section01">
                    <Header />
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <img src="/img/employeeID.png" alt="rising" className="rising-image" style={{ left: '50%' }} />

                    <p className={`intro-title`}>
                        <span className={`intro-01 ${showFirst ? 'fade-in' : ''}`}>{texts[currentIndex]}</span>
                        <br />
                        <span className={`intro-02 ${showSecond ? 'fade-in' : ''}`}>개발자</span>{' '}
                        <span className={`intro-03 ${showThird ? 'fade-in' : ''}`}>최광혁 입니다.</span>
                    </p>
                </div>

                {/* 두 번째 페이지 */}
                <div className="page-section page-2" id="section02">
                    <About scrollbar={scrollbarRef.current} />
                </div>

                {/* 세 번째 페이지 */}
                <div className="page-section page-3">
                    <Spec />
                </div>

                {/* 네 번째 페이지 */}
                <div className="page-section page-4">
                    <Career />
                </div>

                {/* 네 번째 페이지 */}
                <div className="page-section page-5">
                    <Ai />
                </div>

                {/* 다섯 번째 페이지 */}
                <div className="page-section page-6">

                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <div className="rising-circle"></div>
                    <img src="/img/employeeID.png" alt="rising" className="rising-image" style={{ left: '50%' }} />

                    <p className={`intro-title`}>
                        <span className={`intro-02 ${showSecond ? 'fade-in' : ''}`}>이력서</span>{' '}
                        <span className={`intro-03 ${showThird ? 'fade-in' : ''}`}>다운로드</span>
                        
                    </p>
                    <div className="down-container">
                        <Down />
                    </div>
                </div>
            </div>
        </Layout>
    );
};

export default Home;
