import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";

import '../../styles/Project/ProjectDelModal.css';
const ProjectDelModal = ({ title, id, onClose, onConfirm }) => {
  const navigate = useNavigate();
  const [ip, setIp] = useState("");

  useEffect(() => {
    fetch('/cfg/ip.cfg')
      .then(res => res.json())
      .then(data => {
        setIp(data.pc);
        console.log("ip fetched:", data.pc);
      })
      .catch(err => console.error('ip.cfg 불러오기 실패:', err));
  }, []);

  // api 요청 (삭제)
  const handleConfirm = async () => {
    try {
      const response = await fetch(`${ip}/project/${id}`, {
        method: "DELETE",
      });
      if (!response.ok) {
        throw new Error("서버 응답 실패");
      }
      const data = await response.json();
      console.log("프로젝트 삭제 데이터:", data);

      if (onConfirm) {
        onConfirm(id);
      }

      navigate("/", { state: { refresh: true } });

    } catch (error) {
      console.error("삭제 실패:", error);
      alert("서버와 연결할 수 없습니다.");
    }
  };

  return (
    <div className="delete-overlay" onClick={onClose}>
      <div className="delete-box" onClick={(e) => e.stopPropagation()}>
        <h3>"{title}"<br /> 프로젝트를 삭제하시겠습니까?</h3>
        <div className="btn-group">
          <button className="cancel-btn" onClick={onClose}>취소</button>
          <button className="confirm-btn" onClick={handleConfirm}>삭제</button>
        </div>
      </div>
    </div>
  );
};

export default ProjectDelModal;