import { useNavigate } from "react-router-dom";
import '../../styles/About/EmojiModal.css';
import { useState } from "react";

function EmojiModal() {
    const navigate = useNavigate();

    const [showPhone, setShowPhone] = useState(false);

    const showhandleShowPhone = () => {
        setShowPhone(true);
    }

    return (
        <div className="emoji-dialog-overlay">
            <button className="dialog-close" onClick={() => navigate("/")}>x</button>
            <div className="emoji-dialog-box" onClick={(e) => e.stopPropagation()}>
                <img
                    src="../img/profile.jpg"
                    alt="profile-image"
                    className="profile-image"
                />
                <p className='name'>KWANG HYEOK CHOI</p>
                <div className='profile-container'>
                    <img
                        src="../img/phone.png"
                        alt="phone"
                        className="phone-image"
                    />
                    {showPhone ? (
                        <p className="phone-number">010-사5육7-공0팔6</p>
                    ) : (
                        <p className="phone-number" onClick={showhandleShowPhone}>
                            전화번호 보기
                        </p>
                    )}
                    
                    <img
                        src="../img/email.png"
                        alt="email"
                        className="email-image"
                    />
                    <p className="email">rhkdgur120@naver.com</p>
                    <hr className="divider" />
                    <div className="horizontal-images">
                        <a href='https://github.com/806hyogi' target='_blank' rel="noopener noreferrer">
                            <img src="../img/github.png" alt="github" className="circle-image" />
                        </a>
                        <a href='https://www.instagram.com/8.06_hyogi/' target='_blank' rel="noopener noreferrer">
                            <img src="../img/instagram.png" alt="instagram" className="circle-image" />
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EmojiModal;