import React, { useState, useEffect } from "react";
import "../../styles/Ai/AiPage.css";
import Ai from "../Ai/Ai";

const AiPage = React.forwardRef((props, ref) => {
  const [careerData, setcareerData] = useState([]);
  const [skillData, setskills] = useState([]);
  const [introData, setIntroData] = useState([]);
  const [aiResult, setAiResult] = useState("");
  const [isExpanded, setIsExpanded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [ip, setIp] = useState("");

  useEffect(() => {
      fetch('/cfg/ip.cfg')
          .then(res => res.json())
          .then(data => {
              setIp(data.pc);
              console.log("ip fetched:", data.pc);
          })
          .catch(err => console.error('ip.cfg 불러오기 실패:', err));
  }, []);

  useEffect(() => {
    fetch('./cfg/skillsData.cfg')
      .then(res => res.json())
      .then(data => setskills(data))
      .catch(err => console.error('skillsData.cfg 불러오기 실패:', err));
  }, []);

  useEffect(() => {
    fetch('./cfg/introData.cfg')
      .then(res => res.json())
      .then(data => setIntroData(data))
      .catch(err => console.error('introData.cfg 불러오기 실패: ', err));
  }, []);

  useEffect(() => {
    fetch('./cfg/careerData.cfg')
      .then(res => res.json())
      .then(data => setcareerData(data))
      .catch(err => console.error('careerData.cfg 불러오기 실패: ', err));
  }, []);

  // api 요청 (조회)
  const fetchProjects = async () => {
    try {
      const response = await fetch(`${ip}/project`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (!response.ok) {
        throw new Error("서버 응답 실패");
      }

      const data = await response.json();
      return data;

    } catch (error) {
      console.error("업로드 실패:", error);
      alert("서버와 연결할 수 없습니다.");
    }
  };

  const handleClick = async () => {

    const projects = await fetchProjects();
    const payload = {
      careerData,
      skillData,
      introData,
      projects,
    };
    console.log("data: ", payload);
    fetchAiSummary(payload);
  };

  const fetchAiSummary = async (payload) => {
    try {
      setIsLoading(true);

      const response = await fetch(`${ip}/gpt`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ data: payload }),
      });

      if(!response.ok) throw new Error("GPT API 요청 실패");
       
      const result = await response.json();

      console.log("ai 결과물: ", result.summary)
      if (result.summary){
        setAiResult(result.summary);
        setIsLoading(false);
      }

    }catch (error) {
      console.error("GPT 요청 실패: ", error);
      setAiResult("AI 요약을 가져오는 데 실패 했습니다.");
    }
  }

  return (
    <div className="page-content-4" ref={ref}>
      <h1 className="ai-title">AI SUMMARY</h1>
      <p className="ai-subtitle">
        클릭 시 AI가 지원동기, 성향, 목표, 프로젝트, 스펙을 요약해서 보여줍니다.
      </p>
      <div
        className={`ai-card`}
        onClick={handleClick}
      >
        {isLoading ? (
          <div className="spinner-container">
            <div className="spinner"></div>
          </div>
        ) : (
          <Ai />
        )}
      </div>

      <div className="ai-textarea-wrapper">
        <textarea
          className={`ai-textarea ${isExpanded ? "expanded" : ""}`}
          placeholder="AI가 분석한 내용을 확인해보세요..."
          value={aiResult}
          readOnly
        />
        <button
          className="expand-btn"
          onClick={() => setIsExpanded(prev => !prev)}
        >
          {isExpanded ? "-" : "+"}
        </button>
      </div>
    </div>
  );
});

export default AiPage;
