import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import '../../styles/About/MotiModal.css';

function MotiModal() {
    const navigate = useNavigate();

    const [companyName, setCompanyName] = useState("");
    const [inputValue, setInputValue] = useState("");
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('/cfg/ip.cfg')
            .then(res => res.json())
            .then(data => {
                setIp(data.pc);
                console.log("ip fetched:", data.pc);
            })
            .catch(err => console.error('ip.cfg 불러오기 실패:', err));
    }, []);

    useEffect(() => {
        if (!ip) return;
        fetch(`${ip}/moti`)
            .then(res => res.json())
            .then(data => {
                if(data && data.name) setCompanyName(data.name);
            })
            .catch(err => console.error("회사명 가져오기 실패:", err));
    }, [ip]);

    const submit = async () => {
        try {
            const response = await fetch(`${ip}/moti?companyName=${inputValue}`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                }
            });

            const result = await response.text();
            if(result === "success") {
                alert("회사명이 저장되었습니다.");
                setCompanyName(inputValue);
                setInputValue("");
                navigate("/", { state: { updatedCompany: inputValue } });
            }else {
                alert("저장 실패: " + result);
            }
        }catch (error){
            console.error("회사명 저장 실패:", error);
            alert("서버 요청 실패");
        }
    };

    const closeDialog = () => {
        navigate("/", { state: { updatedCompany: companyName } });
    };

    return (
        <div className="moti-dialog-overlay">
            <button className="dialog-close" onClick={closeDialog}>x</button>
            <div className="moti-dialog-box" onClick={(e) => e.stopPropagation()}>
                <div className="moti-first-top">
                    <h3>Q. 목표가 있다면?</h3>
                </div>

                <div className='emph-text-container'>
                    급변하는 <span className='emph-text'>UI/UX</span> 트렌드를 배우고 적용하는 과정에서 큰 즐거움을 느껴왔습니다.<br />
                    새로운 기능을 개발하고, 변화하는 웹의 모습을 눈앞에서 확인할 때마다 큰 성취감을 느끼며,<br />
                    종종 새벽까지 몰입해 있는 제 모습을 보면서 제 길이 바로 이 분야임을 확신하게 되었습니다.<br />
                    저의 목표는 이 업계에서 최고의 전문가로 성장하는 것이며, 그 여정을 <span className='emph-text'>{companyName}</span>과(와) 함께하고 싶습니다.
                </div>

                <div className="moti-first-top">
                    <h3>TO. 회사 이름</h3>
                </div>

                <div className="input-button-row">
                    <input type='text' className='emph-input' value={inputValue} onChange={(e) => setInputValue(e.target.value)} />
                    <button className='emph-summit' onClick={submit}>확인</button>
                </div>
            </div>
        </div>
    );
}

export default MotiModal;