import React, { useEffect, useState } from 'react';
import '../../styles/Project/Project.css';
import { useNavigate, useLocation } from "react-router-dom";

const Spec = React.forwardRef((props, ref) => {

  const [ip, setIp] = useState("");

  useEffect(() => {
    fetch('/cfg/ip.cfg')
      .then(res => res.json())
      .then(data => {
        setIp(data.pc);
        console.log("ip fetched:", data.pc);
      })
      .catch(err => console.error('ip.cfg 불러오기 실패:', err));
  }, []);

  const [activeCards, setActiveCards] = useState(Array(9).fill(false));
  const [cardData, setCardData] = useState([]); // 서버 데이터 저장

  const navigate = useNavigate();
  const location = useLocation();

  const toggleCard = (index) => {
    const newState = [...activeCards];
    newState[index] = !newState[index];
    setActiveCards(newState);
  };


  // api 요청 (조회)
  useEffect(() => {
    if (!ip) return;

    const fetchProjects = async () => {
      try {
        const response = await fetch(`${ip}/project`, {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        });
        if (!response.ok) throw new Error("서버 응답 실패");
        const data = await response.json();
        setCardData(data);
        setActiveCards(Array(data.length).fill(false));
      } catch (error) {
        console.error("프로젝트 조회 실패:", error);
        alert("서버와 연결할 수 없습니다.");
      }
    };

    if (location.state?.refresh) {
      fetchProjects();
      navigate(location.pathname, { replace: true });
    } else {
      fetchProjects();
    }
  }, [ip, location, navigate]);


  return (
    <div className="page-content-2" ref={ref}>
      <h2 className='title'>PROJECT</h2>
      <div className='cardContainer'>
        {cardData.map((card, index) => (
          <div
            key={index}
            className={`card ${activeCards[index] ? 'active' : ''}`}
            onClick={() => toggleCard(index)}
          >
            <div className="side front">
              <div className="img">
                {Array.isArray(card.images) && card.images.length > 0 ? (
                  <img
                    src={`${ip}${card.images[0].filePath}`}
                    alt={card.title}
                    className="card-image"
                  />
                ) : (
                  <span className="no-image-text">회사 저작권 이미지</span>
                )}
              </div>

              <div className="info">
                <h2 className="card-title">{card.title}</h2>
                <p>{card.desc}</p>
              </div>
            </div>
            <div className="side back">
              <div className="info">
                <h2>{card.title}</h2>
                <div className='card-backdesc'>
                  <p>{card.desc}</p>
                  <hr className="divider" />
                  <div>
                    <span className="label">담당 업무: </span>
                    <span className="value">{card.manager}</span>
                  </div>
                  <div className="desc-row">
                    <span className="label">사용 기술: </span>
                    <span className="value">{card.skill}</span>
                  </div>
                  <div className="desc-row">
                    <span className="label">기간: </span>
                    <span className="value">{card.date}</span>
                  </div>
                </div>
                <div className='info-detail-btn-container'>
                  <button className='info-detail-btn'
                    onClick={(e) => {
                      e.stopPropagation();
                      navigate(`/spec/project/${encodeURIComponent(card.title)}`, {
                        state: { project: card, background: location },
                      });
                    }}> 자세히 보기</button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
      {localStorage.getItem("token") && (
        <button
          className="add-card-btn"
          onClick={(e) => {
            e.stopPropagation();
            navigate(`/spec/project/add`, {
              state: { background: location },
            });
          }}
        >+
        </button>
      )}
    </div>
  );
});

export default Spec;