import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import '../../styles/Layout/admin.css';

function AdminModal() {
    const navigate = useNavigate();

    const [isLoggedIn, setIsLoggedIn] = useState(
        !!localStorage.getItem("token")
    );

    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('./cfg/ip.cfg')
            .then(res => res.json())
            .then(data => {
                setIp(data.pc);
                console.log("ip fetched:", data.pc);
            })
            .catch(err => console.error('ip.cfg 불러오기 실패:', err));
    }, []);

    const handleLogin = async () => {

        try {
            const response = await fetch(`${ip}/login`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    id: userId,
                    password: password,
                }),
            });

            const result = await response.text();
            if(result === "success") {
                alert("로그인 성공!");
                setIsLoggedIn(true);
                localStorage.setItem("token", "true");
            }else {
                alert("로그인 실패: 아이디/비밀번호를 확인하세요.");
            }
        }catch (error) {
            console.error("로그인 요청 에러:", error);
            alert("서버와 연결할 수 없습니다.");
        }
    }

    const handleLogout = () => {
        setIsLoggedIn(false);
        localStorage.removeItem("token");
        alert("로그아웃 완료!");
    }

    return (
        <div className="admin-dialog-overlay">
            <button className="dialog-close" onClick={() => navigate("/")}>x</button>
            <div className="admin-dialog-box" onClick={(e) => e.stopPropagation()}>
                <h2>관리자 계정 로그인</h2>
                <hr className="divider" />

                {!isLoggedIn && (
                    <div className="form-group">
                        <input 
                            className="id" 
                            placeholder="아이디 입력" 
                            value={userId}
                            onChange={(e) => setUserId(e.target.value)}
                        />
                        <input 
                            className="password"
                            type="password"
                            placeholder="비밀번호 입력"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />
                    </div>
                )}
                
                <button 
                    className="login-btn"
                    onClick={isLoggedIn ? handleLogout : handleLogin} >
                    {isLoggedIn ? "로그아웃" : "로그인"}
                </button>
            </div>
        </div>
    );
}

export default AdminModal;