import React, { useState, useEffect, useRef } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "../../styles/Layout/header.css";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";


// 공통 컴포넌트
function FloatingLink({ children, href, style }) {
  const [targetOffset, setTargetOffset] = useState({ x: 0, y: 0 });
  const [offset, setOffset] = useState({ x: 0, y: 0 });
  const requestRef = useRef(null);

  const lerp = (start, end, factor) => start + (end - start) * factor;

  useEffect(() => {
    const animate = () => {
      setOffset((prev) => {
        const newX = lerp(prev.x, targetOffset.x, 0.1);
        const newY = lerp(prev.y, targetOffset.y, 0.1);
        return { x: newX, y: newY };
      });
      requestRef.current = requestAnimationFrame(animate);
    };

    requestRef.current = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(requestRef.current);
  }, [targetOffset]);

  const handleMouseMove = (e) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const padding = 60;

    if (
      e.clientX < rect.left - padding ||
      e.clientX > rect.right + padding ||
      e.clientY < rect.top - padding ||
      e.clientY > rect.bottom + padding
    ) {
      setTargetOffset({ x: 0, y: 0 });
      return;
    }

    const relativeX = (e.clientX - rect.left) / rect.width;
    const relativeY = (e.clientY - rect.top) / rect.height;

    const maxMove = 50;
    const x = (relativeX - 0.5) * maxMove;
    const y = (relativeY - 0.5) * maxMove;

    setTargetOffset({ x, y });
  };

  const handleMouseLeave = () => {
    setTargetOffset({ x: 0, y: 0 });
  };

  return (
    <a
      href={href}
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseLeave}
      style={{
        display: "inline-block",
        fontWeight: "bold",
        transform: `translate(${offset.x}px, ${offset.y}px)`,
        transition: "transform 0.05s linear",
        textDecoration: "none",
        cursor: "pointer",
        ...style,
      }}
    >
      {children}
    </a>
  );
}

function BasicExample() {
  const location = useLocation();
  const navigate = useNavigate();
  return (
    <Navbar expand="lg" className="header-main">
      <Container>
        {/* HYOGI */}
        <Navbar.Brand as="span">
          <FloatingLink href="#section01" style={{ fontSize: "30px", color: "rgb(59, 109, 248)" }}>
            HYOGI
          </FloatingLink>
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ms-auto">
            {/* Admin */}
            <Nav.Item as="span"
              onClick={(e) => {
                e.stopPropagation();
                navigate(`/admin`, {
                  state: { background: location },
                });
              }}>
              <FloatingLink style={{ fontWeight: 10, fontSize: "25px", color: "rgb(0, 68, 255)" }}>
                Admin
              </FloatingLink>
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

const Header = () => {
  return (
    <header>
      <BasicExample />
    </header>
  );
};

export default Header;
