import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import '../../styles/Project/ProjectModal.css';
import ProjectDelModal from "./ProjectDelModal";


function ProjectModal() {
    const navigate = useNavigate();
    const location = useLocation();

    const project = location.state?.project || {};

    const [showDeleteModal, setShowDeleteModal] = useState(false); // 삭제 모달
    const [isEditing, setIsEditing] = useState(false); // 수정 상태
    const editTitle = project?.title; // 제목
    const [editDesc, setEditDesc] = useState(project?.desc || ""); // 간단 설명
    const [images, setImages] = useState([]);

    const [editManager, setEditManager] = useState(project?.manager || ""); // 담당 업무
    const [editSkill, setEditSkill] = useState(project?.skill || ""); // 사용 기술
    const [editDate, setEditDate] = useState(project?.date || ""); // 기간
    const [editMember, setEditMember] = useState(project?.member || []); // 인원
    const [editMainFunc, setEditMainFunc] = useState(project?.mainFunc || []); // 주요 기능
    const [editWorkPerformed, setEditWorkPerformed] = useState(project?.workPerformed || []); // 수행 업무
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('/cfg/ip.cfg')
            .then(res => res.json())
            .then(data => {
                setIp(data.pc);
                console.log("ip fetched:", data.pc);
            })
            .catch(err => console.error('ip.cfg 불러오기 실패:', err));
    }, []);

    // 기존 서버 이미지도 File 객체로 변환
    useEffect(() => {
        const loadImages = async () => {
            if (!project.images) return;

            const imgs = await Promise.all(
                project.images.map(async (img, idx) => {
                    const url = `${ip}${img.filePath}`;
                    const response = await fetch(url);
                    const blob = await response.blob();
                    const file = new File([blob], img.fileName || `image${idx}.jpg`, { type: blob.type });

                    return {
                        key: crypto.randomUUID(),
                        file: file,
                        preview: url,
                        existing: true
                    };
                })
            );

            setImages(imgs);
        };

        loadImages();
    }, [project.images, ip]);

    // 사진 추가 / 삭제
    const handleImageUpload = (e) => {
        const file = e.target.files[0];
        if (!file) return;

        const previewUrl = URL.createObjectURL(file);
        setImages((prev) => [
            ...prev,
            { key: crypto.randomUUID(), file, preview: previewUrl }
        ]);
    };

    const handleRemoveImage = (key) => {
        setImages((prev) => prev.filter((img) => img.key !== key));
    };

    // 인원 수정/추가/삭제
    const handleMemberChange = (index, field, newValue) => {
        const updatedMembers = [...editMember];
        updatedMembers[index] = { ...updatedMembers[index], [field]: newValue };
        setEditMember(updatedMembers);
    };

    const handleAddMember = () => {
        setEditMember([...editMember, { key: "", value: "" }]);
    };

    const handleRemoveMember = (index) => {
        if (index !== 0) {
            const updatedMembers = editMember.filter((_, i) => i !== index);
            setEditMember(updatedMembers);
        }
    };

    // 주요 기능 수정/추가/삭제
    const handleMainFuncChange = (index, newValue) => {
        const updated = [...editMainFunc];
        updated[index] = { ...updated[index], value: newValue };
        setEditMainFunc(updated);
    };

    const handleAddMainFunc = () => {
        const newKey = editMainFunc.length > 0
            ? Math.max(...editMainFunc.map(f => f.key)) + 1
            : 1;
        setEditMainFunc([...editMainFunc, { key: newKey, value: "" }]);
    };

    const handleRemoveMainFunc = (index) => {
        if (index !== 0) {
            const updated = editMainFunc.filter((_, i) => i !== index);
            setEditMainFunc(updated);
        }
    };

    // 수행업무 수정/추가/삭제
    const handleWorkChange = (index, field, newValue) => {
        const updated = [...editWorkPerformed];
        updated[index] = { ...updated[index], [field]: newValue };
        setEditWorkPerformed(updated);
    };

    const handleAddWork = () => {
        setEditWorkPerformed([
            ...editWorkPerformed,
            { key: "", value: "" }
        ]);
    };

    const handleRemoveWork = (index) => {
        if (index !== 0) {
            const updated = editWorkPerformed.filter((_, i) => i !== index);
            setEditWorkPerformed(updated);
        }
    };

    const handleDelete = () => {
        // 삭제 api 로직 추가
        console.log(` 삭제 완료`);
        setShowDeleteModal(false);
        navigate("/");
    };

    const closeDialog = () => {
        navigate("/", { state: { refresh: true } });
    };

    const handleSave = async () => {
        const formData = new FormData();

        let title = editTitle;
        let desc = editDesc;
        let manager = editManager;
        let skill = editSkill;
        let date = editDate;
        let member = editMember;
        let mainFunc = editMainFunc;
        let workPerformed = editWorkPerformed;

        const data = {
            title,
            desc,
            manager,
            skill,
            date,
            member,
            mainFunc,
            workPerformed
        };

        formData.append("data", JSON.stringify(data));

        images.forEach((img) => {
            if (img.file) { // file이 있는 경우만 새 이미지
                formData.append("files", img.file);
            }
        });

        console.log("최종 data 객체:", data);
        console.log("최종 images 상태:", images);
        for (let [key, value] of formData.entries()) {
            if (value instanceof File) {
                console.log(`${key}: ${value.name}, size=${value.size}, type=${value.type}`);
            } else {
                console.log(`${key}: ${value}`);
            }
        }

        try {
            const response = await fetch(`${ip}/project`, {
                method: "POST",
                body: formData,
            });

            const result = await response.json();

            alert("프로젝트 저장 성공!");
            console.log("프로젝트 저장 데이터:", result);
        } catch (error) {
            console.error("업로드 실패:", error);
            alert("서버와 연결할 수 없습니다.");
        }

        setIsEditing(false);
    };

    return (
        <div className="modal-overlay">
            <button className="dialog-close" onClick={closeDialog}>x</button>
            <div className="modal-box" onClick={(e) => e.stopPropagation()}>
                {project ? (
                    <>
                        <div className="card-title-container">
                            <h2 className="card-title">{editTitle}</h2>

                            <div className="card-actions">
                                {localStorage.getItem("token") && (
                                    <>
                                        <button
                                            className="del-btn"
                                            onClick={() => setShowDeleteModal(true)}>
                                            <img src="/img/trash.png" alt="삭제" />
                                        </button>

                                        {/* 삭제 모달 */}
                                        {showDeleteModal && (
                                            <ProjectDelModal
                                                title={project.title}
                                                id={project.id}
                                                onClose={() => setShowDeleteModal(false)}
                                                onConfirm={handleDelete}
                                            />
                                        )}
                                        {isEditing ? (
                                            <button
                                                className="save-btn"
                                                onClick={handleSave}>💾
                                            </button>
                                        ) : (
                                            <button
                                                className="edit-btn"
                                                onClick={() => setIsEditing(true)}>✏️
                                            </button>
                                        )}
                                    </>
                                )}
                            </div>
                        </div>
                        <hr className="divider" />
                        {/* 설명 부분 */}
                        {isEditing ? (
                            <textarea
                                className="edit-input-desc"
                                value={editDesc}
                                onChange={(e) => setEditDesc(e.target.value)}
                            />
                        ) : (
                            <p className="card-desc">{editDesc}</p>
                        )}
                        {/* 이미지 부분 */}
                        <div className="image-scroll-wrapper">
                            <div className="image-scroll-container">
                                {images.length > 0 ? (
                                    images.map((image) => (
                                        <div key={image.key} className="scroll-image-wrapper">
                                            <img
                                                src={image.preview} // preview 안에 이미 서버 경로 포함
                                                alt={`프로젝트 이미지`}
                                                className="scroll-image"
                                            />
                                            {isEditing && (
                                                <button
                                                    className="edit-remove-image-btn"
                                                    onClick={() => handleRemoveImage(image.key)}
                                                >
                                                    ❌
                                                </button>
                                            )}
                                        </div>
                                    ))
                                ) : (
                                    <span className="no-image-text">회사 저작권 이미지</span>
                                )}
                            </div>

                            {isEditing && (
                                <label className="add-image-btn">
                                    +
                                    <input
                                        type="file"
                                        accept="image/*"
                                        onChange={handleImageUpload}
                                        style={{ display: "none" }}
                                    />
                                </label>
                            )}
                        </div>

                        {/* 답당 업무 부분 */}
                        {isEditing ? (
                            <div>
                                <span className="label">담당 업무: </span>
                                <input
                                    className="edit-input-manager"
                                    value={editManager}
                                    onChange={(e) => setEditManager(e.target.value)}
                                    placeholder="담당 업무"
                                />
                            </div>
                        ) : (
                            <div>
                                <span className="label">담당 업무: </span>
                                <span className="value">{editManager}</span>
                            </div>
                        )}
                        {/* 사용 기술 부분 */}
                        {isEditing ? (
                            <div className="desc-row">
                                <span className="label">사용 기술: </span>
                                <input
                                    className="edit-input-skill"
                                    value={editSkill}
                                    onChange={(e) => setEditSkill(e.target.value)}
                                    placeholder="사용 기술"
                                />
                            </div>
                        ) : (
                            <div className="desc-row">
                                <span className="label">사용 기술: </span>
                                <span className="value">{editSkill}</span>
                            </div>
                        )}
                        {/* 기간 부분 */}
                        {isEditing ? (
                            <div className="desc-row">
                                <span className="label">기간: </span>
                                <input
                                    className="edit-input-date"
                                    value={editDate}
                                    onChange={(e) => setEditDate(e.target.value)}
                                    placeholder="기간"
                                />
                            </div>
                        ) : (
                            <div className="desc-row">
                                <span className="label">기간: </span>
                                <span className="value">{editDate}</span>
                            </div>
                        )}
                        {/* 인원 부분 */}
                        {isEditing ? (
                            <div className="desc-row">
                                <span className="label">인원: </span>
                                <div className="member-edit-list">
                                    {editMember.map((m, idx) => (
                                        <div key={idx} className="member-edit-item">
                                            <input
                                                className="edit-input-member-key"
                                                value={m.key}
                                                onChange={(e) => handleMemberChange(idx, "key", e.target.value)}
                                                placeholder="역할"
                                            />
                                            <input
                                                className="edit-input-member-value"
                                                value={m.value}
                                                onChange={(e) => handleMemberChange(idx, "value", e.target.value)}
                                                placeholder="인원 수"
                                            />
                                            <button
                                                className="remove-member-btn"
                                                onClick={() => handleRemoveMember(idx)}
                                            >
                                                ❌
                                            </button>
                                        </div>
                                    ))}
                                    <button className="add-member-btn" onClick={handleAddMember}>+ 인원 추가</button>
                                </div>
                            </div>
                        ) : (
                            <div className="desc-row">
                                <span className="label">인원: </span>
                                <span className="value">
                                    {editMember.map((m, idx) => (
                                        <span key={idx}>
                                            {m.key}: {m.value}
                                            {idx < editMember.length - 1 && ", "}
                                        </span>
                                    ))}
                                </span>
                            </div>
                        )}

                        {/* 주요 기능 부분 */}
                        <div className="main-function">
                            <h3 className="main-function-title">🖥️ 주요 기능</h3>

                            {isEditing ? (
                                <div className="main-function-edit">
                                    {editMainFunc.map((func, idx) => (
                                        <div key={func.key} className="main-function-item">
                                            <input
                                                className="edit-input-mainfunc"
                                                value={func.value}
                                                onChange={(e) => handleMainFuncChange(idx, e.target.value)}
                                                placeholder="기능 설명 입력"
                                            />
                                            <button
                                                className="remove-func-btn"
                                                onClick={() => handleRemoveMainFunc(idx)}
                                            >
                                                ❌
                                            </button>
                                        </div>
                                    ))}
                                    <button className="add-func-btn" onClick={handleAddMainFunc}>+ 기능 추가</button>
                                </div>
                            ) : (
                                <ul className="main-function-value">
                                    {editMainFunc.map((func) => (
                                        <li key={func.key}>{func.value}</li>
                                    ))}
                                </ul>
                            )}
                        </div>

                        <div className="work-performed">
                            <h3 className="work-performed-title">⚒️ 수행 업무</h3>

                            {isEditing ? (
                                <div className="work-performed-edit">
                                    {editWorkPerformed.map((work, idx) => (
                                        <div key={idx} className="work-item">
                                            <input
                                                className="edit-input-work-key"
                                                value={work.key}
                                                onChange={(e) => handleWorkChange(idx, "key", e.target.value)}
                                                placeholder="업무 제목"
                                            /><br />
                                            <textarea
                                                className="edit-input-work-value"
                                                value={work.value}
                                                onChange={(e) => handleWorkChange(idx, "value", e.target.value)}
                                                placeholder="업무 설명"
                                            />
                                            <button
                                                className="remove-work-btn"
                                                onClick={() => handleRemoveWork(idx)}
                                            >
                                                ❌
                                            </button>
                                        </div>
                                    ))}
                                    <button className="add-work-btn" onClick={handleAddWork}>+ 업무 추가</button>
                                </div>
                            ) : (
                                <div className="work-performed-value">
                                    {editWorkPerformed.map((work, idx) => (
                                        <div key={work.key || idx}>
                                            {typeof work.key === "string" && (
                                                <div className="work-title">{work.key}</div>
                                            )}
                                            <ul>
                                                <li className="work-value">{work.value}</li>
                                            </ul>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    </>
                ) : (
                    <p>해당 프로젝트 정보를 찾을 수 없습니다.</p>
                )}
            </div>
        </div>
    );
}

export default ProjectModal;