import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useLocation } from "react-router-dom";
import '../../styles/About/about.css';


const About = React.forwardRef(({ scrollbar }, ref) => {
    const introRef = useRef(null);
    const scrollerRef = useRef(null); // SKILL
    const location = useLocation();
    const navigate = useNavigate();

    const handleMouseEnter = () => setIsHovered(true);
    const handleMouseLeave = () => setIsHovered(false);

    const [isHovered, setIsHovered] = useState(false);
    const [isVisible, setIsVisible] = useState(false);
    const [companyName, setCompanyName] = useState("");
    const [introData, setIntroData] = useState([]);
    const [skills, setskills] = useState([]);
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('./cfg/ip.cfg')
            .then(res => res.json())
            .then(data => {
                setIp(data.pc);
                console.log("ip fetched:", data.pc);
            })
            .catch(err => console.error('ip.cfg 불러오기 실패:', err));
    }, []);

    useEffect(() => {
        fetch('./cfg/skillsData.cfg')
            .then(res => res.json())
            .then(data => setskills(data))
            .catch(err => console.error('skillsData.cfg 불러오기 실패:', err));
    }, []);

    useEffect(() => {
        fetch('./cfg/introData.cfg')
            .then(res => res.json())
            .then(data => setIntroData(data))
            .catch(err => console.error('introData.cfg 불러오기 실패: ', err));
    }, []);

    useEffect(() => {
        if (!ip) return;
        
        fetch(`${ip}/moti`)
            .then(res => res.json())
            .then(data => {
                if (data && data.name) setCompanyName(data.name);
            })
            .catch(err => console.error("회사명 가져오기 실패:", err));
    }, [ip]);

    useEffect(() => {
        if (location.state && location.state.updatedCompany) {
            setCompanyName(location.state.updatedCompany);
        }
    }, [location.state]);

    useEffect(() => {
        if (!scrollerRef.current || skills.length === 0) return;
      
        const inner = scrollerRef.current.querySelector(".scroller__inner");
        inner.innerHTML = '';
      
        // 스킬 아이템 생성
        skills.forEach(skill => {
          const li = document.createElement('li');
          li.textContent = skill.name;
          const icon = document.createElement('i');
          icon.className = `bx ${skill.icon}`;
          icon.style.color = skill.color;
          icon.style.marginLeft = "5px";
          li.appendChild(icon);
          inner.appendChild(li);
        });
      
        // 아이템 최소 3세트 반복 → 자연스러운 무한 스크롤
        const children = Array.from(inner.children);
        for (let i = 0; i < 5; i++) {
          children.forEach(child => inner.appendChild(child.cloneNode(true)));
        }
      
        // 한 세트 너비 계산
        const singleSetWidth = inner.scrollWidth / 3;
        inner.style.setProperty('--scroll-width', `-${singleSetWidth}px`);
      
        // 애니메이션 duration 설정
        const scrollSpeed = 50; // px/sec
        const duration = singleSetWidth / scrollSpeed;
        inner.style.animation = `scroll ${duration}s linear infinite`;
      
      }, [skills]);
      
    
    

    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        setIsVisible(true);
                    } else {
                        setIsVisible(false);
                    }
                });
            },
            { threshold: 0.5 }
        );

        if (introRef.current) {
            observer.observe(introRef.current);
        }

        return () => observer.disconnect();
    }, []);

    const renderAnswer = (answerArr) => {
        if (!answerArr) return null;
        const htmlString = answerArr.join('<br/>').replace(/__COMPANY__/g, companyName);
        return <span dangerouslySetInnerHTML={{ __html: htmlString }} />;
    }
    return (
        <div className="page-content" ref={ref}>
            {/* EMOJI */}
            <div className='emoji-container'>
                <img
                    src={isHovered ? '/img/emoji2.png' : '/img/emoji1.png'}
                    alt='err_emoji1_pic'
                    className="emoji1"
                    style={{ cursor: 'pointer' }}
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}
                    onClick={(e) => {
                        e.stopPropagation();
                        navigate(`/about/me`, {
                            state: { background: location },
                        });
                    }}
                />
            </div>

            {/* SKILL */}
            <main>
                <link
                    href="https://unpkg.com/boxicons@2.1.4/css/boxicons.min.css"
                    rel="stylesheet"
                />
                <div className="scroller" ref={scrollerRef}>
                    <ul className="tag-list scroller__inner">
                        {skills.map((skill, i) => (
                            <li key={i}>
                                {skill.name}
                                <i
                                    className={`bx ${skill.icon}`}
                                    style={{ color: skill.color, marginLeft: "5px" }}
                                ></i>
                            </li>
                        ))}
                    </ul>
                </div>
            </main>

            {/* INTRODUCE */}
            <div className={`introduce-container ${isVisible ? "show" : ""}`} ref={introRef}>
                {introData.map((item) => (
                    <div key={item.id} className={item.id}>
                        <div className="intro-first-top">
                            <h3>{item.question}</h3>
                            {item.hasEdit && localStorage.getItem("token") && (
                                <button
                                    className="edit-btn"
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        navigate(`/about/moti`, { state: { background: location } });
                                    }}
                                >
                                    ✏️
                                </button>
                            )}
                        </div>
                        <p>{renderAnswer(item.answer)}</p>
                    </div>
                ))}
            </div>
        </div>
    );
});

export default About;