import React from 'react';
import { BrowserRouter, Routes, Route, useLocation  } from 'react-router-dom';
import Home from './pages/Home/home';
import AdminModal from './components/Layout/AdminModal';
import ProjectModal from './pages/Project/ProjectModal';
import ProjectAddModal from './pages/Project/ProjectInsModal';
import EmojiModal from './pages/About/EmojiModal';
import MotiModal from './pages/About/MotiModal';


function AppRoutes() {
  const location = useLocation();
  const state = location.state || null;

  return (
    <>
      {/* 기본 라우트 */}
      <Routes location={state?.background || location}>
        <Route path="/" element={<Home />} />
        <Route path="/project/:title" element={<ProjectModal />} />
      </Routes>

      {/* 모달 전용 라우트 */}
      {state?.background && (
        <Routes>
          <Route path="/admin" element={<AdminModal />} />
          <Route path="/spec/project/:title" element={<ProjectModal />} />
          <Route path="/spec/project/add" element={<ProjectAddModal />} />
          <Route path="/about/me" element={<EmojiModal />} />
          <Route path="/about/moti" element={<MotiModal />} />
        </Routes>
      )}
    </>
  );
}


function App() {
  return (
    <BrowserRouter
      future={{
        v7_startTransition: true,
        v7_relativeSplatPath: true,
      }}
    >
      <AppRoutes />
    </BrowserRouter>
  );
}

export default App;
